.announcement-popup {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1060;
  overflow-y: hidden;
}
.announcement-popup .modal-dialog {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 800px;
  height: 100%;
}
.announcement-popup .modal-content {
  min-height: 500px;
  background-position: center;
  border: 0;
}
.announcement-popup .modal-body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px 20px 20px auto;
  min-width: 400px;
  background-color: #eee8db;
}
.announcement-popup .modal-body h3 {
  margin-bottom: 1rem;
  color: #233C29;
}
.announcement-popup .modal-body h3 span {
  color: #804431;
}
.announcement-popup .modal-body h6 {
  margin: 0 0 30px;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 0.2em;
  color: #233C29;
}
.announcement-popup .modal-body p {
  line-height: 1.5;
  color: #2F2F2F;
}
.announcement-popup .modal-body p:last-of-type {
  margin-bottom: 0;
}
.announcement-popup .modal-body .close {
  position: absolute;
  top: 15px;
  right: 15px;
  align-items: center;
  justify-content: center;
  display: flex;
  height: 40px;
  width: 40px;
  z-index: 999;
  border: none;
  border-radius: 50px;
  background: #B3CD96;
}
.announcement-popup .modal-body .close .popup-close-icon:before, .announcement-popup .modal-body .close .popup-close-icon:after {
  content: "";
  display: block;
  width: 22px;
  height: 3px;
  background-color: #233C29;
}
.announcement-popup .modal-body .close .popup-close-icon:before {
  transform: rotate(225deg) translate(-7px, -7px);
}
.announcement-popup .modal-body .close .popup-close-icon:after {
  margin-top: 16px;
  transform: rotate(-225deg) translate(-7px, 7px);
}
@media (min-width: 768px) and (max-width: 991px) {
  .announcement-popup .modal-dialog {
    max-width: 700px;
  }
}
@media screen and (max-width: 767px) {
  .announcement-popup .modal-content {
    border-radius: var(--bs-modal-border-radius);
  }
  .announcement-popup .modal-body {
    padding: 0 20px;
  }
  .announcement-popup .modal-body .btn-sage {
    font-size: 14px;
    padding-left: 0;
    padding-right: 0;
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .announcement-popup .modal-dialog {
    margin: 0 auto;
    max-width: 500px;
  }
  .announcement-popup .modal-content {
    min-height: 350px;
    border-radius: var(--bs-modal-border-radius);
  }
  .announcement-popup .modal-body {
    margin: 10px 10px 10px auto;
    width: 300px;
    min-width: inherit;
  }
  .announcement-popup .modal-body .close {
    top: 10px;
    height: 25px;
    width: 25px;
  }
  .announcement-popup .modal-body .close img {
    height: 11px;
  }
  .announcement-popup .modal-body h3 {
    font-size: 18px;
  }
  .announcement-popup .modal-body h6 {
    font-size: 13px;
  }
  .announcement-popup .modal-body p {
    font-size: 14px;
  }
}
@media screen and (max-width: 575px) {
  .announcement-popup .modal-dialog {
    max-width: 355px;
    margin-left: auto;
    margin-right: auto;
  }
  .announcement-popup .modal-content {
    min-height: 450px;
    background-position: 30%;
  }
  .announcement-popup .modal-body {
    margin: 20px auto;
    min-width: inherit;
    width: 320px;
  }
}